:root {
  --WHITE: #fff;
  --Gray-Blue: #6a7b85;
  --Gray: #353d44;
}

* {
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.search {
  background-image: linear-gradient(rgba(253, 252, 252, 0), rgba(255, 255, 255, 0)), url("mountain.b14afb96.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.header-logo {
  letter-spacing: .05em;
  color: var(--WHITE);
  align-content: center;
  padding: 15px 0 15px 5px;
  font-size: 19px;
  font-weight: 200;
  line-height: 1.17;
  display: flex;
}

a {
  color: currentColor;
  text-decoration: none;
}

.container {
  max-width: 1158px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.hero {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 25px 0 70px;
  display: flex;
}

.search-title {
  color: #fff;
  text-shadow: 7px 5px 9px #000;
  margin-bottom: 15px;
  font-size: 41px;
  font-weight: 100;
}

.search-form {
  background-color: var(--WHITE);
  border: none;
  border-radius: 50px;
  width: 350px;
  height: 35px;
  display: flex;
  overflow: hidden;
}

.hero-input {
  text-align: left;
  color: var(--Gray);
  background-color: var(--WHITE);
  border: none;
  outline: rgba(0, 0, 0, 0);
  flex: 1;
  padding-left: 21px;
  font-size: 11px;
  position: relative;
}

.btn {
  letter-spacing: .02em;
  color: var(--WHITE);
  background-color: var(--Gray-Blue);
  cursor: pointer;
  border: none;
  width: 75px;
  padding: 4px;
  font-size: 11px;
  font-style: normal;
  font-weight: 200;
  line-height: 1.5;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

